.popup__button {
  display: flex;
  align-items: center;
  justify-content: center;

  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 15px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-decoration: none;

  margin: 0;
  padding: 0;
  margin-left: auto;
  margin-top: 15px;

  width: 220px;
  height: 46px;

  background: #e27f00;

  border-radius: 2px;

  border: none;

  cursor: pointer;

  &:hover,
  &:focus {
    background: #ffa839;
  }

  &:active {
    background: #da8b26;
  }
}
